body {
  background: #f4f6f8;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 800px;
  margin: auto;
  text-align: center;
}

.form input,
.form textarea {
  width: 100%;
  padding: 10px;
  margin: 5px 0;
}

.form button {
  padding: 10px 20px;
  margin-top: 5px;
}

.notes {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 15px;
  margin-top: 20px;
}

.note {
  background: white;
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.note button {
  background: crimson;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
}
